import React from 'react'
import axios from 'axios'
import {
  changeName,
  initializeForm,
  changeAge,
  requestData,
  receiveDataSuccess,
  receiveDataFailed } from '../actions'

const AddForm = ({ store }) => {
  const { name, age } = store.getState().form  // store

  const handleSubmit = e => {
    e.preventDefault()    // submit

    store.dispatch(requestData())
    axios.post('/api/characters', {
      name,
      age,
    })  // POST
    .then(response => {
      store.dispatch(initializeForm())  // submit
      const characterArray = response.data
      store.dispatch(receiveDataSuccess(characterArray))
    })
    .catch(err => {
      console.error(new Error(err))
      store.dispatch(receiveDataFailed())
    })
  }

  return (
    <div>
      <form onSubmit={e => handleSubmit(e)}>
        <label>
          :
          <input value={name} onChange={e => store.dispatch(changeName(e.target.value))} />
        </label>
        <label>
          :
          <input value={age} onChange={e => store.dispatch(changeAge(e.target.value))} />
        </label>
        <button type="submit">submit</button>
      </form>
    </div>
  )
}

export default AddForm



